/* Typography */
.search_container {
  margin-top: 1em;
  width: 460px;
}
@media only screen and (max-width: 548px) {
  .search_container {
    width: 100%;
  }
}
.search_container .search_title {
  font-family: 'Montserrat', serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
}
.search_container .search_input_content {
  width: 100%;
  position: relative;
  min-height: 48px;
}
.search_container .search_input_content input[type=text] {
  width: 100%;
  height: 100%;
  min-height: 48px;
  border: 2px solid #aaa;
  border-radius: 4px;
  outline: none;
  padding: 8px;
  box-sizing: border-box;
  transition: 0.3s;
  font-family: 'Montserrat', serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
}
.search_container .search_input_content input[type=text]:focus {
  border-color: #FF7300;
}
.search_container .search_input_content input:focus + .fa {
  color: #FF7300;
}
.search_container .search_input_content i {
  position: absolute;
  right: 0;
  padding: 9px 8px;
  color: #aaa;
  transition: 0.3s;
  font-size: 27px;
}
.search_container .search_list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  max-height: 252px;
  overflow-x: scroll;
  border-bottom: 1px solid #D2D2D2;
  border-left: 1px solid #D2D2D2;
  border-right: 1px solid #D2D2D2;
  display: flex;
  flex-direction: column;
}
.search_container .search_list:first-child {
  background-color: #FAFAFA;
}
.search_container .loading input[type=text] {
  border: 2px solid #FF7300;
}
.search_container .loading i {
  color: #FF7300;
}

/* Typography */
.search_list a {
  font-family: 'Montserrat', serif;
  text-decoration: none;
  display: table-cell;
  vertical-align: middle;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #404040;
}
.search_list a .option_list {
  height: 42px;
  padding-left: 16px;
  display: flex;
  align-items: center;
}
.search_list a .option_list .search_bold {
  font-weight: bold;
  background-color: transparent;
}

